.login_page{
    max-width: 400px;
    margin: 0 auto;
    padding: 10px ;
    height: 600px;
    margin-top: 10vh;
    border: 3px solid aliceblue;
    border-radius: 2px;
    text-align: center;
}

.avtar{
    margin-top: 20px;
    width: 30%;
}
.Container{
    margin-top: 30px;

}
.Container label {
    color: white;
    font-size: 20px;
    display: block;
    padding-top: 20px;
}

input{
    width: 50%;
    padding: 12px 20px;
    display: inline-block;
    border: 1px solid #fff;
    border-radius: 50px;

    
}

input::placeholder{
    color: white;
    opacity: 0.7;
    text-align: center;
}

button{
    border: 1px solid #fff;
    padding: 8px;
    border-radius: 50px;
    display: block;
    color: white;
    width: 30%;
    cursor: pointer;
    margin:20px 10px;
}

button:hover {
    background-color: #ffb703;
    border: 1px solid #ffb703;
}


h2{
    padding-top: 10px ;
    text-align: center;
    color: white;
}

input, select, textarea{
    color: #fff;
}
.sign-in {
    margin: 10px;
    color: white;
    font-size: 15px;
}

.sign-in:hover{
    font-size: 16px;
    color: #ffb703;
    cursor: pointer;
}

