.login_page{
    max-width: 400px;
    margin: 0 auto;
    height: 550px;
    margin-top: 5vh;
    border: 3px solid aliceblue;
    border-radius: 2px;
    text-align: center;
}

.avtar2{
    margin-top: 10px;
    width: 30%;
    border-radius: 80%;
}
.Container{
    margin-top: 30px;

}
.Container label {
    color: white;
    font-size: 20px;
    display: block;
    padding-top: 20px;
}

input{
    width: 50%;
    padding: 12px 20px;
    display: inline-block;
    border: 1px solid #fff;
    border-radius: 50px;

    
}

input::placeholder{
    color: white;
    opacity: 0.7;
    text-align: center;
}

button{
    border: 1px solid #fff;
    padding: 8px;
    border-radius: 50px;
    display: block;
    color: white;
    width: 30%;
    cursor: pointer;
    margin:20px 140px;
}

button:hover {
    background-color: #ffb703;
    border: #ffb703;
    
}

h2{
    padding-top: 10px ;
    text-align: center;
    color: white;
}

input, select, textarea{
    color: #fff;
}